.popup__container_image {
    box-sizing: border-box;
    max-width: 75vw;
    max-height: 75vh;
    width: 100%;
    height: 100%;
    padding: 0;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: flex-start;
    border-radius: 10px;
    background-color: inherit;
  }


  @media (max-width: 568px) {
    .popup__container_image {
      height: 300px;
      min-height: 300px;
    }
  }
  